// @flow
// $FlowFixMe
import * as React from 'react';
import type {
	NotifyRenderFunctionType,
	NotifyFunctionOptionsType,
	NotifyProviderPropsType,
	CreateContextType,
} from '../../../material-ui-notify.js.flow';
import NotifyPureRenderer from '../NotifyPureRenderer';

const createContext = (React.createContext: CreateContextType);
export const Context = createContext({ notify: () => undefined });

export type NotifyProviderStateType = {
	renderCurrentNotification?: NotifyRenderFunctionType,
};

class NotifyProvider extends React.Component<NotifyProviderPropsType, NotifyProviderStateType> {
	static displayName = 'NotifyProvider';

	state = {};

	handleAddNotification = (notification: NotifyFunctionOptionsType) => {
		this.stack.push(notification);
		this.iterateStackTillEmpty();
	};

	iterateStackTillEmpty = () => {
		if (this.stack.length <= 0) {
			return;
		}

		// if a notification is currently visible wait
		if (this.isNotificationVisible) {
			setTimeout(this.iterateStackTillEmpty, 200);
			return;
		}

		const { render, delay = 3000, delayBetweenNotifications = 200 } = { ...this.stack[0] };
		this.stack.splice(0, 1);
		this.isNotificationVisible = true;

		this.setState(
			{
				renderCurrentNotification: render,
			},
			() => {
				setTimeout(() => {
					this.setState(
						{
							renderCurrentNotification: undefined,
						},
						() => {
							// delay between notifications
							setTimeout(() => {
								this.isNotificationVisible = false;
							}, delayBetweenNotifications);
						}
					);
				}, delay);
			}
		);
	};

	stack: Array<NotifyFunctionOptionsType> = [];
	isNotificationVisible: boolean = false;

	render() {
		return (
			<Context.Provider
				value={{
					notify: this.handleAddNotification,
				}}
			>
				{this.props.children}
				<NotifyPureRenderer render={this.state.renderCurrentNotification} />
			</Context.Provider>
		);
	}
}

export default NotifyProvider;



