// @flow
/* eslint react/no-multi-comp: 0*/
/* eslint flowtype/require-return-type: 0*/
import React, { Component } from 'react';
import { withStyles } from 'material-ui/styles';
import TemplateExample from '../TemplateExample/TemplateExample';
import { NotifyProvider, Notify } from '../../notify';
import FetchErrorDemo from './FetchErrorDemo';

// types
import type { RenderNotifyPropsType } from '../../../material-ui-notify.js.flow';

export type PageExampleBasicPropsType = {
	classes: Object,
};
export type PageExampleBasicStateType = {
	isLoading: boolean,
	hasError: boolean,
};

class PageExampleBasic extends Component<PageExampleBasicPropsType, PageExampleBasicStateType> {
	static displayName = 'PageExampleBasic';

	state = {
		isLoading: false,
		hasError: false,
	};

	handleSimulateFetchError = () => {
		// this simulates an service call error
		// in real life this should happens in mobx, appolo or if it's an old project in redux
		this.setState(
			{
				isLoading: true,
				hasError: false,
			},
			() => {
				setTimeout(() => {
					this.setState({
						isLoading: false,
						hasError: true,
					});
				}, 500);
			}
		);
	};

	render() {
		const { isLoading, hasError } = this.state;
		return (
			<TemplateExample
				title="Basic example"
				hrefCode="https://github.com/smollweide/material-ui-notify/blob/master/src/gh-pages/PageExampleBasic/PageExampleBasic.js"
			>
				<NotifyProvider>
					<Notify>
						{(notifyProps: RenderNotifyPropsType) => (
							<FetchErrorDemo
								{...notifyProps}
								onFetchData={this.handleSimulateFetchError}
								fetchDataState={{
									isLoading,
									hasError,
								}}
							/>
						)}
					</Notify>
				</NotifyProvider>
			</TemplateExample>
		);
	}
}

export default withStyles({})(PageExampleBasic);



