// @flow
// $FlowFixMe
import * as React from 'react';
import type { NotifyRenderFunctionType } from '../../../material-ui-notify.js.flow';

export type NotifyProviderStateType = {
	renderCurrentNotification?: NotifyRenderFunctionType,
};

export type NotifyPureRendererPropsType = {
	render?: NotifyRenderFunctionType,
};

class NotifyPureRenderer extends React.Component<NotifyPureRendererPropsType> {
	shouldComponentUpdate(nextProps: NotifyPureRendererPropsType) {
		return Boolean(this.props.render !== nextProps.render);
	}

	render() {
		return <div>{this.props.render && this.props.render({})}</div>;
	}
}

export default NotifyPureRenderer;



