// @flow
/* eslint react/no-multi-comp: 0*/
/* eslint flowtype/require-return-type: 0*/
import React, { Component, Fragment } from 'react';
import Snackbar from 'material-ui/Snackbar/Snackbar';
import Slide from 'material-ui/transitions/Slide';
import Button from 'material-ui/Button/Button';
import { withStyles } from 'material-ui/styles';

// types
import type { RenderNotifyPropsType, ClassesType, ThemeType, StylesType } from '../../../material-ui-notify.js.flow';

export type FetchErrorDemoPropsType = RenderNotifyPropsType & {
	onFetchData: () => void,
	fetchDataState: {
		isLoading: boolean,
		hasError: boolean,
	},
	classes: ClassesType,
};

const styles = (theme: ThemeType): StylesType => {
	return {
		row: {
			marginTop: theme.spacing.unit * 20,
			marginBottom: theme.spacing.unit * 20,
			textAlign: 'center',
		},
		button: {},
	};
};

class FetchErrorDemo extends Component<FetchErrorDemoPropsType> {
	static displayName = 'FetchErrorDemo';

	componentWillReceiveProps(nextProps: FetchErrorDemoPropsType) {
		if (!this.props.fetchDataState.hasError && nextProps.fetchDataState.hasError) {
			this.props.notify({
				render: (props: Object) => (
					<Snackbar
						{...props}
						open
						transition={(transitionProps: Object) => <Slide direction="up" {...transitionProps} />}
						SnackbarContentProps={{
							'aria-describedby': 'fetch-error-demo',
						}}
						message={<span id="fetch-error-demo">Upps fetch failed, please try again later!</span>}
					/>
				),
				delay: 2000,
			});
		}
	}

	handleTrigger5Notifications = () => {
		[1, 2, 3, 4, 5].forEach((num: number) => {
			this.props.notify({
				name: num,
				render: (props: Object) => (
					<Snackbar
						{...props}
						open
						transition={(transitionProps: Object) => <Slide direction="up" {...transitionProps} />}
						SnackbarContentProps={{
							'aria-describedby': `multiple-demo-${num}`,
						}}
						message={<span id={`multiple-demo-${num}`}>{`Notification ${num}`}</span>}
					/>
				),
				delay: 2000,
			});
		});
	};

	render() {
		return (
			<Fragment>
				<div className={this.props.classes.row}>
					<Button
						disabled={this.props.fetchDataState.isLoading}
						onClick={this.props.onFetchData}
						variant="raised"
						color="primary"
						className={this.props.classes.button}
					>
						{this.props.fetchDataState.isLoading ? 'Loading ...' : 'Fetch Data'}
					</Button>
				</div>
				<div className={this.props.classes.row}>
					<Button
						onClick={this.handleTrigger5Notifications}
						variant="raised"
						color="primary"
						className={this.props.classes.button}
					>
						Trigger 5 notifications
					</Button>
				</div>
			</Fragment>
		);
	}
}

export default withStyles(styles)(FetchErrorDemo);



