// @flow
// $FlowFixMe
import * as React from 'react';
import type { NotifyPropsType } from '../../../material-ui-notify.js.flow';
import { Context } from '../NotifyProvider/NotifyProvider';

export const Notify = ({ children }: NotifyPropsType) => {
	return (
		<Context.Consumer>
			{context => {
				return children({
					notify: context.notify,
				});
			}}
		</Context.Consumer>
	);
};

export default Notify;



